import axios from "axios";

const API_BASE = "http://localhost:8080/api";

export const getComponents = (type) => axios.get(`${API_BASE}/${type}`);
export const calculate = (data) => axios.post(`${API_BASE}/calculate`, data);
export const calculateSolarPotential = (data) => axios.post(`${API_BASE}/solar-potential`, data);

// New Location APIs
export const getAllRegions = () => axios.get(`${API_BASE}/locations/regions`);

export const getRegionsByCountry = (country) => axios.get(`${API_BASE}/locations/regions/country/${country}`);

export const searchCitiesByName = (name) => axios.get(`${API_BASE}/locations/cities/search?name=${name}`);

export const searchCitiesByPostcode = (prefix) => axios.get(`${API_BASE}/locations/cities/postcode?prefix=${prefix}`);

export const getCitiesByCountry = (country) => axios.get(`${API_BASE}/locations/cities/country/${country}`);

// New NASA POWER API (replaced frontend implementation)
export const fetchSolarData = (latitude, longitude, location) => 
    axios.post(`${API_BASE}/nasa-power/solar-data`, { latitude, longitude, location });

export const fetchSolarDataGet = (latitude, longitude, location) => 
    axios.get(`${API_BASE}/nasa-power/solar-data?latitude=${latitude}&longitude=${longitude}&location=${location || ''}`);

// New Location Specific Yield API
export const calculateLocationSpecificYield = (data) => axios.post(`${API_BASE}/location-yield/calculate`, data);

export const calculateLocationSpecificYieldForMonth = (month, latitude, longitude, location) => 
    axios.get(`${API_BASE}/location-yield/month/${month}?latitude=${latitude}&longitude=${longitude}&location=${location || ''}`);

// New Total Installed Capacity API
export const calculateTotalInstalledCapacity = (data) => axios.post(`${API_BASE}/solar-capacity/calculate`, data);

export const getAllSolarPanelTypes = () => axios.get(`${API_BASE}/solar-capacity/panel-types`);

export const getSolarPanelTypeById = (id) => axios.get(`${API_BASE}/solar-capacity/panel-types/${id}`);

// New Monthly Electricity Generation API
export const calculateMonthlyElectricityGeneration = (data) => axios.post(`${API_BASE}/solar-generation/monthly`, data);

// New Electricity Demand APIs
export const calculateElectricityDemand = (data) => axios.post(`${API_BASE}/electricity-demand/calculate`, data);

export const getElectricityDemandProportions = () => axios.get(`${API_BASE}/electricity-demand/proportions`);

export const validateMonthlyElectricityUsage = (monthlyUsage) => axios.post(`${API_BASE}/electricity-demand/validate-monthly`, monthlyUsage);

// New Gas Demand APIs
export const calculateGasDemand = (data) => axios.post(`${API_BASE}/gas-demand/calculate`, data);

export const getGasDemandProportions = () => axios.get(`${API_BASE}/gas-demand/proportions`);

export const validateMonthlyGasUsage = (monthlyUsage) => axios.post(`${API_BASE}/gas-demand/validate-monthly`, monthlyUsage);